/* @import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Caveat&display=swap');

::-webkit-scrollbar {
    width: 1em;
  }
   
::-webkit-scrollbar-track {
box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
background-color: rgba(150, 3, 26, 0.5);
outline: 1px solid rgba(38, 84, 124, 1);
}

html {
    height: 100%;
}
body {
    margin: 0;
    background-color: rgba(250, 169, 22, 1);
    font-family: 'Caveat', cursive;
    color: rgba(38, 84, 124, 1);
    height: 100%;
    text-align: center;
}

h1 {
    padding: 5px;
    margin: auto;
    margin-top: 0;
    font-size: 5vw;
    width:fit-content;
    text-shadow: 2px 2px 5px;
}

.container{
    display: block;
    margin: auto;
    margin-top: 50px;
    background: none;
    width: 50%;
    min-height: 350px;
    height: fit-content;
    border: 5px solid rgba(38, 84, 124, 0.7);
}

.card-container {
    margin: 10px auto 25px auto;
    display: block;
    width: 90%;
    height: fit-content;
    min-height: 300px;
    max-height: 460px;
    overflow-y: auto;
    border: 2px solid rgba(150, 3, 26, 0.4);
}

.checked {
    text-decoration: line-through;
}

.hide {
    display: none;
}

button:hover {
    cursor: pointer;
}