.input-container {
    display: inline-block;
    width: 90%;
    margin: 15px auto;
}

input, button {
    height: 30px;
    background-color: rgba(150, 3, 26, 0.5);
    color: rgba(160, 193, 185, 1);
    border-radius: 10px;
    border-color: rgba(150, 3, 26, 0.5);
    font-size: 1vw;
}

input {
    /* margin: auto; */
    width: 70%;
    border-bottom: 2px solid rgba(150, 3, 26, 1);
    text-align: center; 
}

button {
    width: 25%;
    margin-left: 2%;
    /* font-size: 80%; */
}

button:hover, input:hover, input:focus {
    background-color: rgba(160, 193, 185, 1);
    color: rgba(150, 3, 26, 1);
    border-color: rgba(160, 193, 185, 1);
}

::-webkit-input-placeholder {
    color: inherit;
}

:-ms-input-placeholder {
    color: inherit;
}

::placeholder {
    color: inherit;
}
