.todo-card {
    margin: auto 20px;
    width: 90%;
    text-align: left;
    font-size: 20px;
    padding-left: 0px;
}

.eachItem {
    width: 100%;
    display: inline-flex;
    justify-content: space-between;
}

li{
    display: flex;
    list-style: none;
    width: 85%;
    max-width: 75%;
    word-wrap:break-word;
}

li:hover {
    cursor: pointer;
}

li p {
    font-size: 2vw;
    font-weight: bold;
    max-width: 100%;
    overflow-wrap: break-word;
    margin: 5px 0;
}

.eachItem button {
    background: none;
    border: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: fit-content;
    padding: 0px;
    margin: 0px;
    justify-content: right;
    align-items: center;
    margin: auto 0 ;
}

